#Hero{
    position: absolute;
    top: 10%;
    left: 10%;
}
.hero_container{
    /* max-width: 100%; */
    display: flex;
    gap: 40px;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    /* flex-wrap: wrap; */
}
.information{
    /* background-color: #aaa; */
    width: fit-content;
}
.information h1{
    font-weight: bold;
    font-size: 45px;
    color: #fff;
}
.information p{
    margin-top: -20px;
    color: #aaa;
    font-size: 13px;
    line-height: 30px;
}
.information .btn{
    width: 33%;
    margin-bottom: 20px;
}

.Hero_image{
    width: 70%;
    height: auto;
    border-radius: 51px;
}
.Hero_image img {
    width: 80%;
    height: 80%;
    border-radius: 15px;
    margin-top: -40px;
}


@media (max-width: 480px) {
    .information{
        max-width: fit-content;
        margin-left: -1%;
    }
    .information h1{
        font-size: 10px;
    }
    .information p{
        text-align: justify;
    }
    .information .btn, #button{
        width: 125%;
    }
    .Hero_image img{
        margin-left: -5%;
        width: 95%;
    }
}

@media (max-width:768px) {
    #Hero{
        left: 2%;
    }
    .area{
        height: 180%;
    }
    .hero_container{
        flex-direction: column;
        max-width: 100%;
    }
    .information{
        width: 90%;
    }
    .information h1{
        font-size: 30px;
    }
    .background{
        width: 20%;
    }
    .Hero_image img{
        width: 88%;
        margin-bottom: 140px;
        margin-top: -8px;
        margin-left: -0.1px;
        
    }
    .information .btn{
        width: 45%;
    }
}

.area{
    background: #000408;  
    /* max-width: 100%; */
    height: 100vh;
}

.circles{
    position: absolute;
    top: 0;
    left: 1%;
    width: 88%;
    /* background-color: #d4a0a0; */
    height: 100%;
    overflow: hidden;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

@keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}